import React from 'react';

function Bar(props) {
    return (
        <div
            className={"d-flex " + (props.contentPosition === 'left' ? 'justify-content-start' : 'justify-content-end')}>
            {props.contentPosition === 'left' ? <div className="content col-5">
                {props.renderContent()}
            </div> : null}
            <div
                className={'d-flex flex-column col-1' + (props.contentPosition === 'left' ? ' pr-0 align-items-end' : ' pl-0 align-items-start')}>
                {props.notchPosition === 'top' ?
                    <span className={'experienceBarNotch ' + props.notchClasses}></span> : null}
                <span className='experienceBar' style={{height: '100%'}}></span>
                {props.notchPosition === 'bottom' ?
                    <span className={'experienceBarNotch ' + props.notchClasses}></span> : null}
            </div>
            {props.contentPosition === 'right' ? <div className="content col-5">
                {props.renderContent()}
            </div> : null}
        </div>
    )
}

function Experience(props) {
    return (
        <section className='container mb-3'>
            <h3 className='mb-4'>Experience</h3>

            <Bar notchPosition='top' contentPosition='left' notchClasses='bar1' renderContent={function () {
                return (
                    <>
                        <h6>JDA Software(Present)</h6>
                        <hr/>
                        <div className='d-flex flex-column align-items-start'>
                            <b className='mb-1'>Assortment Planning</b>
                            <p>This Software is to provide a planning tool for retailers</p>
                        </div>
                    </>
                )
            }}/>
            <Bar notchPosition='top' contentPosition='right' notchClasses='bar2' renderContent={function () {
                return (
                    <>
                        <h6>Altair Engineering PVT Ltd, Bangalore (2017 - 2019)</h6>
                        <hr/>
                        <div className='d-flex flex-column align-items-start'>
                            <b className='mb-1'>Inspire Unlimited</b>
                            <p>Application Is used to Remotely reviewing BOM structure in
                                Inspire and modify the shape and perform analysis for a part generating reports for
                                those jobs.</p>
                        </div>
                    </>
                )
            }}/>
            <Bar notchPosition='top' contentPosition='left' notchClasses='bar3' renderContent={function () {
                return (
                    <>
                        <h6>Altair Engineering PVT Ltd, Bangalore (Nov 2014 - 2017)</h6>
                        <hr/>
                        <div className='d-flex flex-column align-items-start'>
                            <b className='mb-1'>Model Builder</b>
                            <p>Application Is used to do advanced search and downloading data from PDM systems and
                                Remotely reviewing BOM structure and metadata before downloading also performing tasks
                                like batch meshing and CAE modelling task distribution
                                Visualized reports will be generated on the performed tasks.</p>
                        </div>
                    </>
                )
            }}/>
            <Bar notchPosition='top' contentPosition='right' notchClasses='bar4' renderContent={function () {
                return (
                    <>
                        <h6>Infinite Computer Solutions (Nov 2012 – Nov 2014)</h6>
                        <hr/>
                        <div className='d-flex flex-column align-items-start'>
                            <b className='mb-1'>Government Record Management</b>
                            <p>This Software is to manage records like Land, Birth, Death,
                                Marriage...To a user by
                                order his certificates based on the county the fee associated to the items also
                                providing user a search engine to view his documents and generating reports associated
                                to user.</p>
                        </div>
                    </>
                )
            }}/>
        </section>
    )
}

export default Experience;